export default {
	data() {
		return {
			// 微信昵称
			nickname: '陌上花',
			// 主播数
			num: 3,
			// 总收益
			all: 120,
			// 可提现金额
			could: 120.02,
			// 提现模态框的显示隐藏
			showMonmodal: false,
			// 用户信息
			userinfo:{}
		};
	},
	onLoad() {
		this.getuserInfo()
	},
	methods: {
		getuserInfo(){
			this.userinfo = uni.getStorageSync('userinfo');
			console.log(this.userinfo)
		},
		getMoney() {
			this.showMonmodal = true;
			console.log(123)
		},
		handleContact(e) {
			console.log(e.detail.path);
			console.log(e.detail.query);
		},
		closeModal(){
			this.showMonmodal = false
		},
		getRmb(){
			// 提现 点击提现 验证提现金额是否合法 发送请求 关闭模态框
			this.showMonmodal = false
		}
	}
};
